import "vant/es/dialog/style/less";
import _Dialog from "vant/es/dialog";
import { useRouteParams } from '@hooks/useRouteParams';
import { computed, defineComponent } from 'vue';
import { useStore } from 'vuex';
export default defineComponent({
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'FamilyMemeber',
  props: {
    showForm: Boolean,
    families: {
      type: Array,
      required: true
    }
  },
  emits: ['show-family-form'],
  components: {
    [_Dialog.name]: _Dialog
  },

  setup() {
    const store = useStore();
    const {
      creditUserId
    } = useRouteParams();

    const removeFamily = id => {
      _Dialog.confirm({
        title: '确定解绑?',
        message: '解约后，您绑定的亲情账号也不能，使用医信付支付!',
        confirmButtonColor: '#2879fc'
      }).then(() => {
        console.log('解绑操作确认');
        let urlParams = {
          data: {
            creditUserId,
            familyUserId: id
          }
        };
        store.dispatch('familyModule/removeFamilies', urlParams);
      }).catch(() => {
        console.log('解绑操作取消');
      });
    };

    return {
      removeFamily,
      familyLimit: computed(() => store.state.familyModule.familyLimit),
      familySize: computed(() => store.state.familyModule.familSize)
    };
  }

});